'use server';

/**
 * @fileOverview A message polishing AI agent.
 *
 * - polishMessage - A function that handles the message polishing process.
 * - PolishMessageInput - The input type for the polishMessage function.
 * - PolishMessageOutput - The return type for the polishMessage function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const PolishMessageInputSchema = z.object({
  message: z.string().describe('The message to be polished.'),
});
export type PolishMessageInput = z.infer<typeof PolishMessageInputSchema>;

const PolishMessageOutputSchema = z.object({
  polishedMessage: z.string().describe('The polished message.'),
});
export type PolishMessageOutput = z.infer<typeof PolishMessageOutputSchema>;

export async function polishMessage(input: PolishMessageInput): Promise<PolishMessageOutput> {
  return polishMessageFlow(input);
}

const prompt = ai.definePrompt({
  name: 'polishMessagePrompt',
  input: {schema: PolishMessageInputSchema},
  output: {schema: PolishMessageOutputSchema},
  prompt: `You are an AI message polishing agent. You will be given a message and you will rewrite it to improve its tone, clarity, and emotional impact. Return the polished message.

Message: {{{message}}}`,
});

const polishMessageFlow = ai.defineFlow(
  {
    name: 'polishMessageFlow',
    inputSchema: PolishMessageInputSchema,
    outputSchema: PolishMessageOutputSchema,
  },
  async input => {
    const {output} = await prompt(input);
    return output!;
  }
);
